n/* Reset */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #a94257;
  color: #333;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-container {
  padding: 1rem;
  width: 100%;
  max-width: 600px;
}

.game-wrapper {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 25px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Added header margin-bottom */
header {
  margin-bottom: 0rem; /* smaller gap */
}

header h1 {
  font-family: Georgia, serif;
  font-size: 2.5rem;
  margin: 0;
  text-align: center;
  user-select: none;
}

header p {
  text-align: center;
  color: #555;
  font-weight: 600;
  margin-bottom: 0rem;
}

.message-area {
  min-height: 1.6rem; /* just enough for one line */
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  margin-bottom: 0rem;
}

.message {
  background-color: #333;
  color: white;
  padding: 0.3rem 0.9rem;
  border-radius: 9999px;
  font-weight: 700;
  font-size: 0.95rem;
  line-height: 1;
  animation: fadeOut 2.5s ease-out forwards;
}

@keyframes fadeOut {
  0% { opacity: 1; }
  90% { opacity: 1; }
  100% { opacity: 0; }
}

.revealed-groups {
  max-height: none !important;
  overflow-y: visible !important;
  margin-bottom: 8px;
}

.group-box {
  padding: 0.5rem 1rem;
  border-radius: 10px;
  border: 2px solid #ccc;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  text-align: center;
  font-weight: 600;
  text-transform: uppercase;
  user-select: none;
  margin-bottom: 10px;
  font-size: 0.9rem;
  line-height: 1.25;
}

.group-box h3 {
  margin: 0;
  font-size: 1rem;
}

.group-box p {
  margin: 0.25rem 0 0;
  font-size: 0.9rem;
  font-weight: 500;
}

.bg-yellow-100 {
  background-color: #f8df6d;
  border-color: #f8df6d;
  color: #1d1c1c;
}

.bg-green-100 {
  background-color: #a1c45b;
  border-color: #a1c45b;
  color: #1d1c1c;
}

.bg-blue-100 {
  background-color: #b2c4f0;
  border-color: #b2c4f0;
  color: #1d1c1c;
}

.bg-purple-100 {
  background-color: #b981c6;
  border-color: #b981c6;
  color: #1d1c1c;
}

.words-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.6rem;
}

.word-button {
  aspect-ratio: 1 / 1;
  width: 100%;
  background-color: #f4f4f4;
  border: 2px solid #ccc;
  border-radius: 12px;
  cursor: pointer;
  font-weight: 700;
  text-transform: uppercase;
  user-select: none;
  transition: all 0.2s ease;
  color: #444;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.3rem;
  text-align: center;
  line-height: 1.1;
  white-space: normal;
  word-break: break-word;
  font-size: clamp(0.55rem, 1.5vw, 1rem);
  flex-direction: column;
}

/* Hover only on unselected buttons */
.word-button:hover:not(:disabled):not(.selected) {
  background-color: #e0e0e0;
}

.word-button.selected {
  background-color: #333;
  color: white;
  border-color: #222;
  box-shadow: 0 0 10px #555;
  transform: scale(1.05);
}

.word-button.incorrect {
  animation: shake 0.8s ease-in-out;
  background-color: #fddede;
  border-color: #f59696;
  color: #8b0000;
}

.bottom-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.mistakes {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  user-select: none;
}

.dots {
  display: flex;
  gap: 0.3rem;
}

.dot {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 2px solid #333;
}

.dot.filled {
  background-color: #333;
}

.dot.empty {
  background-color: transparent;
}

.submit-button {
  padding: 0.5rem 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 9999px;
  border: none;
  background-color: #333;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s ease;
  user-select: none;
}

.submit-button:disabled {
  background-color: #aaa;
  cursor: not-allowed;
}

.submit-button.reveal {
  background-color: #a94257; /* e.g., amber */
  color: white;
}

@keyframes shake {
  0%, 20%, 40%, 60%, 80%, 100% { transform: translateX(0); }
  10%, 30%, 50%, 70%, 90% { transform: translateX(-8px); }
}

@media (max-width: 480px) {
  /* 1) Full‐screen centering using the small‐viewport unit */
  body {
    margin: 0;
    height: 100svh;            /* use small‐viewport height */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    padding-top: env(safe-area-inset-top);
    padding-bottom: env(safe-area-inset-bottom);
  }

  /* 2) Wrapper fits exactly, scrolls internally if needed */
  .game-wrapper {
    width: calc(100% - 1rem);
    max-height: calc(100svh - (env(safe-area-inset-top) + env(safe-area-inset-bottom) + 2rem));
      /* 2rem for top/bottom breathing room */
    padding: 1rem;
    overflow-y: auto;
    box-shadow: none;
    border-radius: 16px;
  }

  /* 3) Tighter header & grid so nothing overflows */
  header {
    margin-bottom: 0.5rem;
  }
  header h1 { font-size: 1.6rem; }
  header p  { font-size: 0.85rem; }

  .words-grid { gap: 0.35rem; }
  .word-button { font-size: 0.6rem; padding: 0.2rem; }

  /* 4) Bottom‑bar stacks and full width */
  .bottom-bar {
    flex-direction: column;
    gap: 0.4rem;
  }
  .submit-button { width: 100%; }
  .mistakes span { font-size: 0.7rem; }
}

